import { Component, OnInit } from '@angular/core';
import { UsermanagementService } from '../../services/usermanagement.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, Validators } from '@angular/forms';
declare var google: any;

@Component({
  selector: 'app-useredit',
  templateUrl: './useredit.component.html',
  styleUrls: ['./useredit.component.scss']
})
export class UsereditComponent implements OnInit {
  userForm: any;
  driverid;
  strVal;
  lastChar;
  userdata;
  arrdata;
  profilepic;
  public lat: any = '';
  public lng: any = '';
  public address: any = '';

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private service: UsermanagementService,
    private _router: Router,
  ) {

    this.route.params.subscribe((params) => {
      this.driverid = params['driverid'];
      console.log(this.driverid)
    });

    this.userForm = this.formBuilder.group({
          'id': [this.driverid, Validators.required],
          'name': ['', Validators.required],
          'email': ['', Validators.required],
          'phoneNumber': ['', Validators.required],
          'countryCode': ['', Validators.required],
          'businessName': ['', Validators.required],
          'jhaihoURL': ['', Validators.required],
          'address': ['', Validators.required],
          'customAddress': [''],
          'addressLocality': [''],
          'geoAddress': [''],
          'profileImage': [''],
    });

  }
  
  getCodeHtml(data: any): any {
    let _temp: any = JSON.stringify(data);
    _temp = _temp.split(',').join(',<br>');
    _temp = _temp.split('{').join('{<br>');
    _temp = _temp.split('}').join('<br>}');
    return _temp;
  }

  autoCompleteCallback1(data: any): any {
    this.address = JSON.stringify(data.data.formatted_address);
    this.lat = JSON.stringify(data.data.geometry['location'].lat);
    this.lng = JSON.stringify(data.data.geometry['location'].lng);
    this.arrdata = [ this.lat, this.lng ];
    this.userForm.controls['address'].setValue(this.address);
    this.userForm.controls['geoAddress'].setValue(this.arrdata);
   // this.userForm.controls['longitude'].setValue(this.lng);
    
  //  console.log(this.address)
 //   console.log(this.lat)
    //console.log(this.lng)

       var map;
      // var myLatlng = new google.maps.LatLng(40.713956, -74.006653);
       var myLatlng = new google.maps.LatLng(this.lat, this.lng);
   
       var myOptions = {
           zoom: 8,
           center: myLatlng
          // mapTypeId: google.maps.MapTypeId.ROADMAP
       };
   
       map = new google.maps.Map(document.getElementById("map_canvas"), myOptions);
   
       var marker = new google.maps.Marker({
           draggable: true,
           position: myLatlng,
           map: map,
           title: this.address
       });
  }
  initialize(a) {
    var map;
   // var myLatlng = new google.maps.LatLng(40.713956, -74.006653);
    var myLatlng = new google.maps.LatLng(a[0], a[1]);

    var myOptions = {
        zoom: 8,
        center: myLatlng
       // mapTypeId: google.maps.MapTypeId.ROADMAP
    };
    map = new google.maps.Map((document.getElementById('map_canvas')), myOptions);

    var marker = new google.maps.Marker({
        draggable: true,
        position: myLatlng,
        map: map,
        title: "Your location"
    });
 
  }
  saveUser() {
    if (this.userForm.dirty && this.userForm.valid) {
       this.service.updateDriver(this.userForm.value).subscribe(
            data => {
                console.log(data)
                 alert(`User details updated successfully`);
                 this._router.navigate(['/usermanagement']);
            },
            error => {
              alert(error);
               console.log('error')
            });
       
    }
  }

  ngOnInit() {
    this.getUserDetails();
    
  }
 
  
  myUploader(event1) {
    this.service.changeProfilePicture(event1).subscribe(
      data => {
      
        this.userForm.controls['profileImage'].setValue(data.data.url);
       // console.log(data.data.url)
         // this._router.navigate(['/usermanagement']);
      },
      error => {
         console.log('error')
      });
   
   }

  getUserDetails() {
    
    this.service.getDriverDetail(this.driverid).subscribe(
      (res) => {
      // console.log(res.data.geoAddress)

       this.initialize(res.data.geoAddress);
        this.userdata = res.data;
        this.profilepic = res.data.profileImage;
        const value = { 
          id: this.driverid, 
          name: this.userdata.name, 
          email: this.userdata.email,
          phoneNumber: this.userdata.phoneNumber,
          countryCode: this.userdata.countryCode,
          businessName: this.userdata.businessName,
          jhaihoURL: this.userdata.profileURL,
          address: this.userdata.address,
          customAddress: this.userdata.customAddress,
          addressLocality: this.userdata.addressLocality,
          geoAddress:this.userdata.geoAddress,
          profileImage: this.userdata.profileImage
        };
        this.userForm.setValue(value);
       
        console.log(this.userdata)
        //this.driver.profileImg = this.APIbaseUrl + this.driver.profileImg;
      },
      (error) => {
      //  this.toast.error(error.message);
      })
  }

  onChange(){


    this.strVal = this.userForm.value.phoneNumber.toString();
    this.lastChar = this.strVal.charCodeAt(this.strVal.length - 1);

    // if(this.lastChar == 101 || this.lastChar == 43 || this.lastChar == 45){
    //   this.strVal = this.strVal.substring(0, this.strVal.length - 1);
    // }
    if(this.lastChar < 48 || this.lastChar > 57){
      this.strVal = this.strVal.substring(0, this.strVal.length - 1);
    }

    
    this.userForm.controls['phoneNumber'].setValue(this.strVal);
    
  }
 
}
