import { Component, OnInit } from '@angular/core';
import { UsermanagementService } from '../../services/usermanagement.service';
import { PagerService } from '../../services/pager.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Ng2PaginationModule } from 'ng2-pagination';
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormControl } from '@angular/forms';
import { FormGroup } from '@angular/forms';
declare var moment:any;

@Component({
  selector: 'app-usermanagementinfo',
  templateUrl: './usermanagementinfo.component.html',
  styleUrls: ['./usermanagementinfo.component.scss']
 
})
export class UsermanagementinfoComponent implements OnInit {


  closeResult: string;
  Drivers: any[];
  Driverslist: any[];
  name: string;
  pages: any;
  counts: any;
  searchText: any;
  driverRef: any = null;
  daturi:any = "";
  daturi2:any = "";
  daturi3:any = "";
  daturi4:any = "";
  daterange:any = "";
  startdate:any = "";
  enddate:any = "";
  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];
  Math: any;

  users = [];
  custom = "";
 
  totalItem;
  vehicleRef: any = null;
  modalRef: any;
  userid: any;
  reason: any;
  msg: any = null;

  devices: any[] = [{'name':'All', 'value':'All'},{'name':'iOS', 'value':'2'}, {'name':'Android', 'value':'1'}];
  allstatus: any[] = [{'name':'All', 'value':'All'}, {'name':'Active','value':'1'}, {'name':'InActive','value':'0'}, {'name':'Blocked','value':'4'}];
  completes: any[] = [{'name':'All', 'value':'All'}, {'name':'0-10','value':'1'}, {'name':'11-20','value':'2'}, {'name':'21-30','value':'3'}, {'name':'91-100','value':'4'}];
  periods: any[] = [{'name':'All', 'value':'All'},
  {'name':'Last Week', 'value':'1'},
  {'name':'Last Month', 'value':'2'},
  {'name':'Last 3 Months', 'value':'3'},
  {'name':'Last 6 Months', 'value':'4'},
  {'name':'Last Year', 'value':'5'},
  {'name':'Custom Date', 'value':'6'}
 ];
  default: string = 'All';
  searchForm: FormGroup;

  constructor(
    private service: UsermanagementService,
    private pagerService: PagerService,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private _router: Router
  ) {

    this.searchForm = new FormGroup({
      device: new FormControl(null),
      status: new FormControl(null),
      complete: new FormControl(null),
      period: new FormControl(null),
      date1: new FormControl(null),
      date2: new FormControl(null),
    });
   
    this.searchForm.controls['device'].setValue(this.default, {onlySelf: true});
    this.searchForm.controls['status'].setValue(this.default, {onlySelf: true});
    this.searchForm.controls['complete'].setValue(this.default, {onlySelf: true});
    this.searchForm.controls['period'].setValue(this.default, {onlySelf: true});
    this.Math = Math;
   }

  public  searchUser(){
    console.log(this.searchForm.value)
  }
  ngOnInit() {
    
   this.getServerData(1);
 
  }

  public getServerData(event){

        let data = this.searchForm.value;
       // console.log(data.date1)
       // console.log(data.date2)
        if(data.date1 !=''){
       
         // this.startdate= data.date1;
          let ts = moment(data.date1, "M/D/YYYY H:mm").valueOf();
          let m = moment(ts);
         // let s = m.format("M/D/YYYY H:mm");
          this.startdate = m;
          ///alert("Values are: ts = " + m + ", s = " + ts);
        }else{
          this.startdate= '';
        }

        if(data.date2 !=''){
          let ts = moment(data.date2, "M/D/YYYY H:mm").valueOf();
          let m = moment(ts);
          this.enddate=m;
        }else{
          this.enddate= '';
        }
        
        if(data.period != 'All'){
           this.custom  = data.period;
        }else{
          this.custom  = '';
        }
        if(data.device != null && data.device != 'All'){
          this.daturi = '&deviceType='+data.device;
         }else{
          this.daturi = ""; 
         }
         if(data.status != null && data.status != 'All'){
            this.daturi2 = '&active='+data.status;
         }else{
          this.daturi2 = ""; 
         }
         if(data.complete != null && data.complete != 'All'){
            this.daturi3 = '&deviceType=1';
         }else{
          this.daturi3 = ""; 
         }
         if(data.period != null && data.period != 'All' && data.period != '6'){
            this.daturi4 = '&period='+data.period;
         }else{
          this.daturi4 = ""; 
         }
         if(this.enddate != '' && this.startdate != ''){
            this.daterange ="&startTime"+this.startdate+"&endTime="+this.enddate; 
         }else{
           this.daterange  = "";
         }
        let alldata =  this.daturi+this.daturi2+this.daturi3+this.daturi4+this.daterange;
	   	this.service.getdata(event, alldata).subscribe(
			response =>{
          this.totalItem = response.data.count;
          this.users = response.data;
       },
			error =>{
				alert('Server error');
			}
		);
		return event;
  }
  

  

  archiveUser(id){
   
    this.service.removeDriver(id,this.reason).subscribe(res => {
    //  console.log(res);
      console.log('User deleted successfully');
      this.getServerData(1);
      this.modalRef.close();
    //  this.d();
      //this._router.navigate(['/usermanagement']);
    });
  }

  blockUser(id){
   
    this.service.blockUser(id).subscribe(res => {
     console.log(res);
      console.log('User Blocked successfully');
      this.getServerData(1);
      this.modalRef.close();
    //  this.d();
      //this._router.navigate(['/usermanagement']);
    });
  }

 
shapes = [
    {name:'Profile Completed', id:'complete'},
    {name:'Profile Incomplete', id:'incomplete'},
];

shape: string;

open(content) {
  this.modalRef = this.modalService.open(content);
  this.modalRef.result.then((result) => {
      this.vehicleRef = null;
  }, (reason) => {
      this.vehicleRef = null;
  });
}
onSelect() {
     this.service.getDriversfilter(this.shape).subscribe(res => {
       
      this.Drivers = res.data;
      this.Driverslist = res.data.users;
      this.pages = res.data.page;
      this.counts = res.data.count;
     
    });
}
delete_u(modal, userid) {
  this.vehicleRef = userid;
  this.open(modal);
}

  
}
