import { Component, OnInit } from '@angular/core';
import { UsermanagementService } from '../../services/usermanagement.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Ng2PaginationModule } from 'ng2-pagination';
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-archiveuser',
  templateUrl: './archiveuser.component.html',
  styleUrls: ['./archiveuser.component.scss']
})
export class ArchiveuserComponent implements OnInit {

     
      closeResult: string;
      Drivers: any[];
      Driverslist: any[];
      name: string;
      pages: any;
      counts: any;
      driverRef: any = null;
      
      // pager object
      pager: any = {};
      // paged items
      pagedItems: any[];
      Math: any;
    
      arusers = [];
      vehicleRef: any = null;
      modalRef: any;
      userid: any;
      reason: any;
      msg: any = null;
    
      constructor(
        private service: UsermanagementService,
         private route: ActivatedRoute,
        private modalService: NgbModal,
        private _router: Router
      ) {
    
        this.Math = Math;
       }
    
      ngOnInit() {
        
       this.getServerData_arch(1);
     
      }
    
      public getServerData_arch(event){
    		this.service.getdataarchive(event).subscribe(
    			response =>{
             	this.arusers = response.data;
    					
    			},
    			error =>{
    				alert('Server error');
    			}
    		);
    		return event;
      }
      
    
      
    
      archiveUser(id){
       
        this.service.removeDriverp(id).subscribe(res => {
        //  console.log(res);
          console.log('User deleted successfully');
          this.getServerData_arch(1);
          this.modalRef.close();
        //  this.d();
          //this._router.navigate(['/usermanagement']);
        });
      }
    
      blockUser(id){
       
        this.service.recoverUser(id).subscribe(res => {
         console.log(res);
          console.log('User recovered successfully');
          this.getServerData_arch(1);
          this.modalRef.close();
        //  this.d();
          //this._router.navigate(['/usermanagement']);
        });
      }
    
    open(content) {
      this.modalRef = this.modalService.open(content);
      this.modalRef.result.then((result) => {
          this.vehicleRef = null;
      }, (reason) => {
          this.vehicleRef = null;
      });
    }
    
    delete_u(modal, userid) {
      this.vehicleRef = userid;
      this.open(modal);
    }
    
}
