import { Component, OnInit } from '@angular/core';
import { UsermanagementService } from '../../services/usermanagement.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-customerinfo',
  templateUrl: './customerinfo.component.html',
  styleUrls: ['./customerinfo.component.scss']
})
export class CustomerinfoComponent implements OnInit {

  driverid;
  userdata;
  customerId;
  customer_appoinments_data;
  getsbo_profile_info;
  sbo_login_data;
  appointmentCustomer:any;
  profileImage:any;
  objectKeys = Object.keys;
  bookingtype= ['Created by SBO','Created by Customer ( Web app user )']
  activeStatus=['User is inactive/logout','User is active','-','-','User is blocked','User is deleted'];
  appointmentStatus=['Request by Business User','Request by Web User','Accepted by Business User','Cancel Appointment from  Business User','Cancel Appointment from User','Request by Business User','Reschedule by Business User','Reschedule by User','Accepted by User','Decline by Business User','Decline by Business User','Reschedule Decline by SBO','Complete Appointment SBO'];
  constructor(
    private route: ActivatedRoute,
    private service: UsermanagementService,
    private _router: Router,
  ) { 
    this.route.params.subscribe((params) => {
      this.driverid = params['driverid'];
   //   console.log(this.driverid)
    })
  }

  ngOnInit() {
    this.getUserDetails();

  }

  getAppointmentWithCustomer(customerId){
    this.service.getappointment_with_customer(this.driverid,customerId).subscribe(
      (res) => {
       return this.appointmentCustomer = res.data;
       
        //this.driver.profileImg = this.APIbaseUrl + this.driver.profileImg;
      },
      (error) => {
      //  this.toast.error(error.message);
      });
  }
  
  getUserDetails() {
     
      this.service.getDriverDetail(this.driverid).subscribe(
      (res) => {
       return this.userdata = res.data;
       },
      (error) => {
      //  this.toast.error(error.message);
      });
     
      this.service.get_customer_appoinments_info(this.driverid).subscribe(
        (res) => {
        
          return this.customer_appoinments_data = res.data.appointments;
         
        },
        (error) => {
        //  this.toast.error(error.message);
        });

        
  }


}
