import { Component, OnInit } from '@angular/core';
import { UsermanagementService } from '../../services/usermanagement.service';
import { PagerService } from '../../services/pager.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Ng2PaginationModule } from 'ng2-pagination';
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-systemusers',
  templateUrl: './systemusers.component.html',
  styleUrls: ['./systemusers.component.scss']
})
export class SystemusersComponent implements OnInit {

  closeResult: string;
  Drivers: any[];
  Driverslist: any[];
  name: string;
  pages: any;
  counts: any;
  driverRef: any = null;
  searchText: any;
  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];
  Math: any;

  users = [];
  totalItem;
  vehicleRef: any = null;
  modalRef: any;
  userid: any;
  reason: any;
  msg: any = null;

  constructor(
    private service: UsermanagementService,
    private pagerService: PagerService,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private _router: Router
  ) {

    this.Math = Math;
   }

  ngOnInit() {
    
   this.getServerData(1);
 
  }

  public getServerData(event){
		this.service.getsystemdata(event).subscribe(
			response =>{
        console.log(response)
          this.totalItem = response.data.count;
					this.users = response.data;
					
			},
			error =>{
				alert('Server error');
			}
		);
		return event;
  }
  

  

  archiveUser(id){
   
    this.service.removeDriver(id,this.reason).subscribe(res => {
    //  console.log(res);
      console.log('User deleted successfully');
      this.getServerData(1);
      this.modalRef.close();
    //  this.d();
      //this._router.navigate(['/usermanagement']);
    });
  }

  blockUser(id){
   
    this.service.blockUser(id).subscribe(res => {
     console.log(res);
      console.log('User Blocked successfully');
      this.getServerData(1);
      this.modalRef.close();
    //  this.d();
      //this._router.navigate(['/usermanagement']);
    });
  }

 
shapes = [
    {name:'Profile Completed', id:'complete'},
    {name:'Profile Incomplete', id:'incomplete'},
];

shape: string;

open(content) {
  this.modalRef = this.modalService.open(content);
  this.modalRef.result.then((result) => {
      this.vehicleRef = null;
  }, (reason) => {
      this.vehicleRef = null;
  });
}
onSelect() {
     this.service.getDriversfilter(this.shape).subscribe(res => {
       
      this.Drivers = res.data;
      this.Driverslist = res.data.users;
      this.pages = res.data.page;
      this.counts = res.data.count;
     
    });
}
delete_u(modal, userid) {
  this.vehicleRef = userid;
  this.open(modal);
}
}
